.advantages {
    padding-top: 40px;
    background-color: var(--bgc-accent-ice);
}

.advantages__container {
    margin-left: auto;
    margin-right: auto;
}

.advantages__list {
    @include mq(tablet-only) {
        display: flex;
        justify-content: space-between;
    }

    @include mq(desktop) {
        display: flex;
        justify-content: space-around;
    }
}

.advantages__item {
    margin-bottom: 32px;

    &:last-child {
        margin-bottom: 0;
    }

    @include mq(tablet) {
        margin-bottom: 0;
    }
}

.advantages__title {
    margin-bottom: 4px;

    transition: var(--slow-anim);

    @include mq(tablet-only) {
        margin-bottom: 13px;
    }

    @include mq(desktop) {
        margin-bottom: 16px;
    }
}

.advantages__icecream {
    &::before {
        content: '';
        display: block;
        width: 33px;
        height: 50px;

        margin-bottom: 16px;

        @include mq(tablet-only) {
            margin-bottom: 36px;

        }

        @include mq(desktop) {
            margin-bottom: 40px;

        }

        background-image: url(/src/images/advantages/icecream.svg);
        background-size: contain;
        background-repeat: no-repeat;
        fill: var(--cl-txt-accent-dark);
    }

    &:first-child {
        margin-top: 0;
    }
}

.advantages__apples {
    &::before {
        content: '';
        display: block;
        width: 50px;
        height: 50px;

        margin-bottom: 16px;

        @include mq(tablet-only) {
            margin-bottom: 36px;

        }

        @include mq(desktop) {
            margin-bottom: 40px;
        }

        background-image: url(/src/images/advantages/apples.svg);
        background-size: contain;
        background-repeat: no-repeat;
        fill: var(--cl-txt-accent-dark);
    }
}

.advantages__heart {
    &::before {
        content: '';
        display: block;
        width: 44px;
        height: 50px;

        margin-bottom: 16px;

        @include mq(tablet-only) {
            margin-bottom: 36px;

        }

        @include mq(desktop) {
            margin-bottom: 40px;

        }

        background-image: url(/src/images/advantages/heart.svg);
        background-size: contain;
        background-repeat: no-repeat;
        fill: var(--cl-txt-accent-dark);
    }
}


.advantages__text {
    width: 230px;
    color: var(--cl-txt-main);

    &:last-child {
        margin-bottom: 0;
    }

    @include mq(tablet) {
        margin-bottom: 0;
    }

    @include mq(tablet-only) {
        width: 162px;
    }
}